:root{
    --f-color : #1e0bc8;
    --s-color : #05aede;
    --t-color : #010004;
    --bg1-color : #a4a6a7;
    --bg2-color : #ffffff;
    --primary-color: #2c3e50;      /* Dark blue */
    --secondary-color: #3498db;    /* Blue */
    --accent-color: #e74c3c;       /* Red */
    --background-color: #ecf0f1;   /* Light grey */
}
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
.header-container {
    color: var(--bg2-color);
    width: 100%;
    height: 5rem;
    top: 0;
    background-color: var(--primary-color);
    padding-top: 1rem;
    padding-left: 1.3rem;
    display: inline-flex;
    position: absolute;
}
.header-container ul{
    list-style-type: none;
    display: flex;
    margin-left: 4rem;
}
.header-container ul li{
    margin:0.3rem 1.5rem 0 0;
    padding: 5px 8px 5px 5px;
}
.header-container ul li a{
    text-decoration: none;
    color:var(--bg2-color);
    font-size: 18px;
}
.header-container ul li a:hover{
    cursor: pointer;
    color: var(--f-color);
    transition: 0.4s;
    position: relative;
}
.header-container h1{
    color: var(--f-color);
    margin-left: 14.5rem;
}
.header-container h2{
    margin-left: 0.7rem;
    margin-top: 7.5px;
    font-size: 28px;
    font-family: Arial;
}
.header-container ul li.active{
    border: 2px solid blue;
    border-radius: 10px;
    color: var(--f-color);
    transition: 0s;
}
.app-logo{
    background-color: white;
    width: 30px;
    height: 30px;
    border-radius: 30px;
    margin-top: 0.5rem;
}
@media (max-width: 768px) {
    .header-container {
        flex-direction: column;
        align-items: center;
        height: auto;
        padding: 1rem;
        position: relative;
    }

    .header-container ul {
        flex-direction: column;
        margin-left: 0;
        margin-top: 1rem;
    }

    .header-container ul li {
        margin: 0.5rem 0;
    }

    .header-container h1, .header-container h2 {
        margin-left: 0;
        text-align: center;
        font-size: 1.5rem;
    }
}

@media (max-width: 576px) {
    .header-container {
        padding: 0.5rem;
        text-align: center;
    }
    .header-container ul li{
        border-bottom: white 1px solid;
    }
    .header-container ul li a {
        font-size: 16px;

    }

    .header-container h2 {
        font-size: 24px;
    }
}